.App {
  text-align: center;
  background-color: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}
.question {
  margin-bottom: 50px;
}
.question .MuiChip-root {
  font-size: 1.8rem;
  padding: 30px;    
  margin-bottom: 10px;
}
.answer button {
  font-size: 2rem;
  margin: 20px;
  min-width: 200px;
  text-transform: lowercase;
}
.listCards{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-around;
}